////
/// External Image Link Strip
/// @group components
////


.external-img-link-strip {
    @extend %grid__12;
    text-align: center;

    a {
        @include anchor(external-img);
    }


    img {
        filter: grayscale(100%);
        height: 70px;
    }

    img, a {
        display: inline-block;
    }

    > img, > a {
        margin: 0 $measure-medium;
    }
}
