////
/// Primary Navigation
/// @group components
////


.primary-navigation {
    @include grid-column(8, "medium", true, true);
    @include grid-column(6, "large", false, false);
    align-self: center;

    @include media("below:medium") {
        display: none;
    }
}

.primary-navigation__list {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    overflow: hidden;

    li {
        display: inline-block;
    }

    a {
        @include anchor(primary-navigation);
    }
}
