/****************************
*    Footer - version 1     *
****************************/
.footer {
    @include media("above:medium") {
        background-color: $color-black;
        background-image: linear-gradient(to right, $color-black 0%, $color-black 50%, $color-lightgray 50%, $color-lightgray 100%);
    }
}

.footer__left-side {
    background-color: $color-black;
    color: $color-white;
}

.footer__right-side {
    background-color: $color-lightgray;
    color: $color-black;
}

.footer__headlines {
    padding-top: $measure-enormous;
    padding-bottom: $measure-enormous;

    @include media("above:medium") {
        padding-bottom: $measure-medium;
    }
}

.footer__main-headline {
    font-size: $font-size-beta;
    line-height: $line-height-small;
    margin-bottom: $measure-small;
}

.footer__sub-headline {
    text-transform: uppercase;
    font-size: $font-size-small;
    margin-bottom: $measure-enormous;
}

.footer__cta-headline {
    font-size: $font-size-gamma;
    margin-top: $measure-enormous;

    a {
        @include anchor(dark-background);
        @include anchor(right-arrow);
    }
}

.footer__details {
    padding-top: $measure-enormous;
    padding-bottom: $measure-medium;
    text-align: center;

    @include media("above:medium") {
        text-align: left;
    }
}

.footer__address {
    display: block;
    margin-bottom: $measure-large;
    font-style: normal;
}

.footer__address-part {
    display: block;
    font-size: $font-size-small;
}

.footer__address-part--org {
    margin-bottom: $measure-small;
    font-size: $font-size-gamma;
    font-weight: $font-weight-medium;
}

.footer__social {
    margin-bottom: $measure-large;

    a {
        @include anchor(reset);
        margin-right: $measure-medium;
        margin-bottom: $measure-small;
        color: $color-lucifer;
        font-size: $font-size-gamma;

        span:not([class]) {
            @include visually-hidden;
        }
    }
}

.footer__social-title {
    display: block;
    margin-bottom: $measure-small;
    font-weight: $font-weight-medium;
}

.footer__misc-links {
    margin-bottom: $measure-large;

    a {
        display: inline-block;
        margin-right: $measure-small;
        margin-bottom: $measure-small;
        font-size: $font-size-small;
    }
}

.footer__accreditation {
    a {
        @include anchor(reset);
        display: inline-block;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;
    }

    img {
        height: 30px;
    }
}

.footer__accreditation-title {
    display: block;
    margin-bottom: $measure-small;
    font-weight: $font-weight-medium;
}

.footer__copyright {
    margin-bottom: $measure-large;
    font-size: $font-size-small;

    span {
        display: block;
    }
}


/****************************
*    Footer - version 2     *
****************************/
.footer2 {
    background-color: $color-lightgray;
    padding-top: $measure-enormous;
    padding-bottom: $measure-enormous;
}

.footer2__section {
    padding-top: $measure-medium;
    padding-bottom: $measure-medium;
    text-align: center;

    @include media("above:medium") {
        text-align: left;
    }
}

.footer2__section--second {
    border-top: $border-style-default $color-gallery $border-width-outline;
    padding-top: $measure-large;
}

.footer2__address {
    margin-bottom: $measure-medium;
    font-style: normal;
}

.footer2__address-organisation {
    margin-bottom: $measure-small;
    font-size: $font-size-gamma;
    font-weight: $font-weight-medium;
}

.footer2__address-details {
    display: block;
    font-size: $font-size-small;

    span {
        display: block;
        margin-bottom: $measure-small;
    }

    .footer__address-phone {
        @include media("above:medium") {
            margin-top: $measure-large;
        }
    }

    a {
        @include anchor(skate);
    }
}

.footer2__misc-links {
    margin-bottom: $measure-large;
    font-size: $font-size-small;

    li {
        display: inline;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;

        @include media("above:medium") {
            display: block;
        }
    }

    a {
        @include anchor(skate);
    }
}

.footer2__social {
    font-size: $font-size-delta;

    li {
        display: inline;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;

        @include media("above:medium") {
            display: block;
        }
    }

    a {
        @include anchor(skate);
        color: $color-lucifer;

        span:not([class]) {
            //@include anchor(skate);
            font-size: $font-size-small;
            margin-left: $measure-small;
            vertical-align: middle;

            @include media("below:medium") {
                @include visually-hidden;
            }
        }
    }
}

.footer2__social-heading {
    display: block;
    margin-bottom: $measure-small;
    font-weight: $font-weight-medium;

    @include media("above:medium") {
        @include visually-hidden;
    }
}

.footer2__accreditation {
    a {
        @include anchor(simple-image);
        display: inline-block;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;
    }

    img {
        height: 35px;
    }
}

.footer2__accreditation-title {
    display: block;
    margin-bottom: $measure-medium;
    font-weight: $font-weight-medium;

    @include media("above:medium") {
        display: inline-block;
    }
}

.footer2__copyright {
    font-size: $font-size-small;

    span {
        display: block;
    }

    a {
        @include anchor(skate);
        vertical-align: middle;
    }
}
