.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
}

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul {
    margin: 0;
    padding: 0;
}

main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

strong {
    font-weight: 700;
}

a,
button {
    color: inherit;
    transition: all 0.2s ease;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: 0;
    overflow: visible;
    font: inherit;
    letter-spacing: inherit;
    -webkit-font-smoothing: inherit;
    cursor: pointer;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

img {
    max-width: 100%;
    height: auto;
    border: 0;
}

html {
    box-sizing: border-box;
    background-color: #fafafa;
    min-height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    position: relative;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: #ffffff;
    color: #1e1e1e;
}

html {
    font-family: "PT Sans", Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.375;
}

/**
 * Content Elements
 */
/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Meta Sans", Arial, Helvetica, sans-serif;
    font-display: swap;
    width: 100%;
    display: block;
    flex: 1 0 auto;
    position: relative;
    margin-bottom: 1rem;
    font-weight: 400;
}

.alpha,
h1,
.banner__text .banner__text__subtitle {
    font-size: 33.1776px;
    font-size: 2.0736rem;
    line-height: 1.327;
}

.beta,
h2 {
    font-size: 27.648px;
    font-size: 1.728rem;
    line-height: 1.592;
}

.gamma,
h3 {
    font-size: 23.04px;
    font-size: 1.44rem;
    line-height: 1.91;
}

.delta,
h4 {
    font-size: 19.2px;
    font-size: 1.2rem;
    line-height: 1.146;
}

.epsilon,
h5 {
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.375;
}

.zeta,
h6 {
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.375;
}

p {
    margin: 0 0 1.375em;
}

p:empty {
    display: none;
    visibility: hidden;
}

@media (min-width: 1201px) {
    p.columns {
        text-align: justify;
        column-count: 2;
        column-gap: 2em;
    }
}

/**
