    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1rem;
    font-style: italic;
    font-size: 14px;
    line-height: 1.2;
}

figure > a {
    display: block;
}

/**
 * Quotes
 */
blockquote {
    hanging-punctuation: first;
}

blockquote,
:lang(en) blockquote {
    quotes: "“" "”" "‘" "’";
}

:lang(de) blockquote,
:lang(pl) blockquote {
    quotes: "„" "“" "‚" "‘";
}

:lang(fr) blockquote {
    quotes: "« " " »" "‹ " " ›";
}

blockquote p:before {
    content: open-quote;
}

blockquote p:after {
    content: close-quote;
}

blockquote p:not(:last-of-type):after {
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    border: 0 !important;
    overflow: hidden !important;
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
}

small {
    font-size: 14px;
}

sub,
sup {
    position: relative;
    vertical-align: baseline;
    line-height: 0;
}

sup {
    top: -0.5em;
}

sub {
    bottom: -0.25em;
}

a {
    transition: all 0.2s ease;
    color: #005ea5;
    border-bottom-color: #005ea5;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    text-decoration: none;
    position: relative;
}

a:hover {
    background-color: #005ea5;
    color: #ffffff;
}

a:focus {
    outline: 3px solid #1e1e1e;
}

a:active {
    outline: 2px dotted #1e1e1e;
}

h1 > a,
h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a {
    border-bottom-width: 2px;
}

.dark-background a {
    color: #888888;
    border-bottom-color: #d92231;
}

.dark-background a:hover {
    background-color: #d92231;
    color: #ffffff;
}

.dark-background a:focus, .dark-background a:active {
    outline-color: #ececec;
}

.skip {
    position: absolute;
    top: 2px;
    left: 2px;
    display: block;
}

.skip:not(:hover):not(:focus):not(:active) {
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    border: 0 !important;
    overflow: hidden !important;
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
}

a.button {
    display: inline-block;
}

button,
.button {
    padding: 0.5em;
}

button:focus,
.button:focus {
    outline: 1px dotted #1e1e1e;
}

/**
 * Fix `audio`, `iframe`, and `video`
 */
audio,
iframe,
video {
    width: 100%;
    border: 0;
    overflow-x: hidden;
}

iframe {
    contain: strict;
}

/**
 * Images and Figures
 */
img,
svg,
picture {
    display: block;
    overflow: hidden;
    position: relative;
}

img,
svg,
picture,
a img,
a
svg,
a
picture {
    width: auto;
    max-width: 100%;
    height: auto;
}

img {
    border-width: 0;
}

svg:not(:root) {
    overflow: hidden;
}

/**
 * Top-Level Grid
 */
.container, .header__wrapper, .accordion-tabs--top-level-layout .accordion-tabs__menu-wrapper, .banner {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    max-width: 1440px;
    clear: both;
    zoom: 1;
}

@supports (display: flex) {
    .container, .header__wrapper, .accordion-tabs--top-level-layout .accordion-tabs__menu-wrapper, .banner {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
    }
