@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fade-out {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes rotate-horizontal-in {
    from {
        transform: rotateY(-90deg);
    }
    to {
        transform: none;
    }
}

@keyframes rotate-vertical-in {
    from {
        transform: rotateX(-90deg);
    }
    to {
        transform: none;
    }
}

@keyframes rotate-horizontal-out {
    from {
        transform: none;
    }
    to {
        transform: rotateY(-90deg);
    }
}

@keyframes rotate-vertical-out {
    from {
        transform: none;
    }
    to {
        transform: rotateX(-90deg);
    }
}

