.bracket-block, .profile-bracket-block {
    width: 100%;
    padding: 1rem 0.5rem;
    position: relative;
}

.bracket-block:after, .profile-bracket-block:after, .bracket-block:before, .profile-bracket-block:before {
    content: " ";
    width: 1rem;
    height: 100%;
    border-style: solid;
    border-color: #d92231;
    z-index: -1;
}

.bracket-block:before, .profile-bracket-block:before {
    position: absolute;
    top: 0;
    left: 0;
    border-width: 0.5rem 0 0.5rem 0.5rem;
}

.bracket-block:after, .profile-bracket-block:after {
    position: absolute;
    right: 0;
    bottom: 0;
    border-width: 0.5rem 0.5rem 0.5rem 0;
}

