////
/// Nav Mobile
/// @group components
////


.nav-mobile {
    $goto-width: 50px;

    width: grid-column-width(2);

    @include media("above:medium") {
        display: none;
    }

    .nav-mobile__page-link,
    .nav-mobile__close-link,
    .nav-mobile__toggle-link {
        display: block;
        border-width: 0;
        padding: 10px 15px;
        vertical-align: baseline;
        line-height: 1.5em;
    }

    .nav-mobile__toggle-link {
        font-size: 0.8em;
    }

    .nav-mobile__page-link {
        margin-right: $goto-width;
    }

    .nav-mobile__close-link,
    .nav-mobile__toggle-link {
        @include absolute(top-right);
        height: 100%;

        span {
            font-size: 1.5em;
        }
    }

    .nav-mobile__close-link span {
        @extend .fa-close;
    }

    .nav-mobile__toggle-link span {
        @extend .fa-plus;
    }

    .nav-mobile__level.open > .nav-mobile__level__header .nav-mobile__toggle-link span {
        @extend .fa-minus;
    }

    $level-colors: #ffffff, #ffffff, #ffffff, #ffffff, #f2f2f2, #e6e6e6, #dadada;
    @for $i from 1 through length($level-colors) {
        .nav-mobile__level--l#{$i - 1} {
            $color: nth($level-colors, $i);
            background-color: $color;
            color: contrasting-color($color);

            @if ($i > 3) {
                & > .nav-mobile__level__header {
                    padding-left: $measure-medium;
                }
            }
        }
    }
    .nav-mobile__level--l1 > .nav-mobile__level__header {
        text-transform: uppercase;
    }

    .nav-mobile__level--l1,
    .nav-mobile__level--l2 {
        & > .nav-mobile__level__header {
            font-weight: $font-weight-medium;
        }
    }

    .nav-mobile__level--l2 {
        & > .nav-mobile__level__header, > ul {
            border-bottom: 1px solid #bbbbbb;
        }
    }

    .nav-mobile__page-link--t2 {
        span {
            border-bottom: $border-width-default $border-style-default currentColor;
        }
    }

    .nav-mobile__level__header {
        position: relative;

    }

    .nav-mobile__level {
        ul:not([class]) {
            margin: 0;
        }

        li {
            border-bottom: $border-width-hairline $border-style-default #ebebeb;
            position: relative;
        }

        &.nav-mobile__level--left {
            right: 100%;
            left: -100%;
        }
        &.nav-mobile__level--center {
            right: 0;
            left: 0;
        }
        &.nav-mobile__level--right {
            right: -100%;
            left: 100%;
        }

        ul {
            display: none;
        }

        &.open > ul {
            display: block;
        }
    }
}

.nav-mobile__toggle,
.nav-mobile__content {
    display: none;
}

.nav-mobile__content {
    @include animation(slide-right-out);
}

.nav-mobile__toggle-label {
    @include non-selectable;
    display: block;
}

.nav-mobile__toggle-label__icon {
    @extend .fa !optional;
    @extend .fa-bars !optional;
}

/** open state **/

.nav-mobile__toggle:checked + .nav-mobile__overlay {
    .nav-mobile__toggle-label {
        @extend .fa-close !optional;
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        position: fixed;
        top: 0;
        right: $grid-padding;
        z-index: z-index(dialog);
    }
    .nav-mobile__content {
        @include animation(slide-right-in);
        color: $color-black;
        background-color: $color-white;
        // width: 100%;
        min-width: $fixed-sidebar-width;
        height: 100%;
        display: block;
        position: fixed;
        top: 0;
        right: 0;
        z-index: z-index(modal);
    }

    .nav-mobile__toggle-label__icon {
        @extend .fa-close !optional;
    }
}

/** scrolling inside menu support **/
.nav-mobile__overlay {
    overflow-y: auto;
}

@supports (display: flex) {

    .nav-mobile__overlay {
        overflow-y: hidden;
    }

    .nav-mobile__container {
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .nav-mobile__content {
        flex: 1 1 auto;
        overflow-y: auto;
    }
}

/** no js **/

.no-js .nav-mobile__content:not(.nav-mobile__content--nojs) {
    display: none;
    flex: 0;
}
