////
/// Bracket Block
/// @group components
////


.bracket-block {
    width: 100%;
    padding: $measure-medium $measure-small;
    position: relative;

    &:after,
    &:before {
        content: " ";
        width: $measure-medium;
        height: 100%;
        border-style: $border-style-default;
        border-color: $color-alizarin;
        z-index: z-index(below);
    }

    &:before {
        @include absolute(top-left);
        border-width: $measure-small 0 $measure-small $measure-small;
    }
    &:after {
        @include absolute(bottom-right);
        border-width: $measure-small $measure-small $measure-small 0;
    }
}

.profile-bracket-block {
    @extend .bracket-block;
    @extend .grid;

    .profile-bracket-block__img {
        float: left;
    }
}

.profile-bracket-block__img {
    @extend .grid__2;
    align-self: center;
}

.profile-bracket-block__text {
    @extend .grid__10;
    align-self: center;
}

.profile-bracket-block__text__name {
    @include font-family(meta);
    font-size: $font-size-gamma;
    font-weight: $font-weight-light;
}
