////
/// CTA Group
/// @group components
////


.cta-group {
    & + & {
        @include media("below:medium") {
            margin-top: $measure-medium;
        }
    }
}

.cta-group--bottom {
    align-self: flex-end;
}

.cta-group--vertical {
    text-align: center;

    .cta + .cta {
        margin-top: $measure-medium;
    }
}

.cta-group--overhang-bottom {
    position: relative;
    height: 100%;

    .cta {
        @include media("above:medium") {
            width: 100%;
            min-height: 100%;
        }
    }

    .cta-group--overhang-bottom__icon {
        @supports (display: flex) {
            @include media("above:medium") {
                position: absolute;
                bottom: #{$measure-medium * 1.5};
                left: $grid-padding;
            }
        }
    }
}
