////
/// Dialog
/// @group components
////


.dialog {
    background: rgba($color-black, 0.7);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: z-index(modal);
}

.dialog__box {
    width: auto;
    max-width: calc(100% - 1rem);
    height: auto;
    max-height: calc(100% - 1rem);
    display: inline-block;
    flex: auto;
    overflow-y: auto;

    @include media("above:medium") {
        max-width: 50%;
        max-height: 70%;
    }
}

.dialog__box__heading {
    @include font-family(meta);
    background-color: $color-white;
    padding: $measure-medium;
    font-size: $font-size-beta;
    font-weight: $font-weight-light;
    text-align: center;
}

.dialog__box__content {
    background-color: $color-gallery;
    padding: $measure-medium;
}


.dialog__box__heading__close {
    float: right;
}

.dialog__box__heading__close__icon {
    @extend .fa;
    @extend .fa-close;
}
