////
/// Tertiary Navigation
/// @group components
////


.tertiary-navigation {
    position: absolute;
    bottom: 100%;
    right: $grid-padding;

    @include media("below:medium") {
        display: none;
    }
}

.tertiary-navigation__list {
    font-size: 0;

    li {
        display: inline-block;
        font-size: $font-size-default;
    }

    a {
        @include anchor(tertiary-navigation);
    }
}

.tertiary-navigation__toggles {
    @include media("above:medium") {
        display: none;
    }
}
