////
/// Nav Desktop Variation 2
/// @group components
////

.header--2,
.header:not([class*="header--"]) {
    .header__notice,
    .header__toggles,
    .breadcrumbs,
    .breadcrumbs-dropdown {
        display: none;
    }

    .tertiary-navigation__listtt a,
    .tertiary-navigation__search,
    .nav-mobile__toggle-label {
        padding-top: $measure-enormous;
        padding-bottom: $measure-medium;
        font-weight: $font-weight-bold;
        text-transform: uppercase;

        span {
            border-bottom: $border-width-default $border-style-default $transparent;
            font-size: $font-size-small;
        }

        &:hover,
        &:focus {
            span {
                border-bottom-color: currentColor;
            }
        }
    }

    .tertiary-navigation__nav-mobile {
        @include media("above:small") {
            display: none;
        }
    }

    .tertiary-navigation__search,
    .tertiary-navigation__nav-mobile {
        display: inline-block;
    }

    .tertiary-navigation__listtt {
        @include media("below:extralarge") {
            margin-top: $measure-medium;
            position: absolute;
            top: 100%;
            right: 0;
            white-space: nowrap;
        }

        &,
        li {
            display: inline-block;
        }

        a {
            color: $color-emperor;
            padding-left:  $measure-small;
            padding-right: $measure-small;
            font-size: $font-size-small;
        }
    }

    .tertiary-navigation__search,
    .nav-mobile__toggle-label {
        background-color: $color-alizarin;
        color: $color-white;
        align-self: flex-end;
        display: inline-block;
        border-width: 0;
        vertical-align: top;
        text-align: center;

        @include media("below:small") {
            max-width: #{1.5rem + $measure-medium * 2};
            padding-left:  $measure-medium;
            padding-right: $measure-medium;
        }

        @include media("above:small") {
            max-width: #{1.5rem + $measure-large * 2};
            padding-left:  $measure-large;
            padding-right: $measure-large;
        }

        &:before {
            @extend .fa !optional;
            font-size: $font-size-gamma;
            font-weight: $font-weight-default;
        }
    }

    .tertiary-navigation__search {
        &:before {
            content: $fa-var-search;
        }
    }

    .nav-mobile__toggle-label {
        @include transition;

        &:before {
            content: $fa-var-bars;
        }

        &.open {
            &:before {
                content: $fa-var-close;
            }
        }

        .fa {
            display: none;
        }
    }

    .tertiary-navigation__listttt + .tertiary-navigation__search {
        margin-left: $measure-large;
    }

    & ~ main aside {
        min-width: 250px;
        float: left;
        margin-right: $measure-large;
    }

    & ~ main .side-menu {
        border: $border-width-default $border-style-default $color-emperor;
        max-width: 100%;
        margin-bottom: $measure-large;

        li {
            display: block;
        }

        li + li {
            border-top: $border-width-default $border-style-default $color-emperor;
        }

        a {
            color: inherit;
            display: block;
            padding: $measure-small;
            border-bottom-width: 0;
            font-weight: $font-weight-bold;
            z-index: z-index(above);

            span {
                border-bottom: $border-width-hairline $border-style-default $transparent;
            }

            &:hover,
            &:focus,
            &:active {
                span {
                    border-bottom-color: currentColor;
                }
            }
        }
    }
}
