////
/// Accordion Tabs
/// @group components
////

.tabs {
    background: $color-white;
}

.tabs__content__group {
    overflow: hidden;
    padding: $measure-large;
}

.tabs__content__group__shame {
    .tabs__content__heading-anchor.active + & {
        @include animation(slide-top-in);
    }
}

.js {
    .tabs {
        margin-bottom: $measure-large;
    }

    .tabs__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;
        }
    }

    .tabs__content__group > div {
        padding-top: $measure-large;
    }

    .tabs__content__heading-anchor {
        @include visually-hidden;
    }

    .tabs__content__heading-anchor__icon {
        @extend .fa;
        @extend .fa-plus;
        float: right;
    }

    [aria-selected="true"] .tabs__content__heading-anchor__icon {
        @extend .fa-minus;
    }
}

.tabs__content__heading {
    @include visually-hidden
}

.tabs__menu {
    display: flex;
    flex-direction: row;
}

.tabs__menu__item {
    @include anchor(activable-inline-normal);
    font-size: $font-size-default;

    flex: 1 1 auto;

    display:flex;
    align-items: center;
    justify-content: center;
}

.tabs__content__heading-anchor {
    @include anchor(activable-stacked);
}
