////
/// Header
/// @group layout
////


.header {
    display: flex;
    flex-wrap: wrap;
    padding-top: $measure-large;
    padding-bottom: $measure-large;
}

.header__wrapper {
    @extend .container;
}

.header__notice {
    background-color: $color-gallery;
    color: inherit;
    width: 100%;
    display: block;
    flex: 1 0 100%;
    padding: $measure-small;
    border-bottom-width: 0;
    position: relative;
    top: -$grid-padding;
    left: 50%;
    line-height: $line-height-small;
    text-align: center;
    transform: translateX(-50%);

    span {
        border-bottom: $border-width-hairline $border-style-default $transparent;

        &:after {
            content: " →";
        }
    }

    &:hover,
    &:focus {
        span {
            border-bottom-color: currentColor;
        }
    }

    &:active {
        background-color: $color-black;
        color: contrasting-color($color-black);
    }
}

.header__logo {
    $header-logo-width: 140px;
    $header-logo-ratio: 0.656;
    @include grid-column(3, "medium", true, false);
    @include hide-text;
    width: grid-column-width(6);
    align-self: center;

    a,
    img {
        width: $header-logo-width;
        display: block;
    }

    a {
        @include anchor(reset);
        min-height: floor($header-logo-width * $header-logo-ratio);
    }
}

.header__search {
    @include grid-column(1, "medium", true, false);
    @include grid-column(3, "large", false, false);
    width: grid-column-width(4);
    align-self: center;
    text-align: right;
}

.header__search__button {
    margin-right: -$measure-small;

    &:before {
        @include font-family(icon);
        content: $fa-var-search;
        font-size: $font-size-delta;
    }
}

.header__toggles {
    text-align: right;
}
