////
/// Key Info
/// @group components
////

.key-info {
    margin-top: $measure-large;
    position: relative;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.key-info__item {
    margin: 0 $measure-large $measure-large;
}

.key-info__item__prop {
    color: $color-emperor;
}

.key-info__item__value {
    color: $color-orange;
}

.key-info__item--feature {
    align-items: center;
    display: flex;

    .key-info__item__prop,
    .key-info__item__value {
        display: inline-block;
    }

    .key-info__item__prop {
        font-weight: $font-weight-bold;
        text-transform: uppercase;
        margin-right: $measure-small;
        color: lighten($color-emperor, 15%);
    }
}

