// Put your crappy CSS here, and we can revisit it later so we can integrate it
// in our codebase properly! :)

.anchor-demo-list {
    @extend .default-list;

    li {
        padding: $measure-medium;

        @for $i from 0 through 10 {
            &:nth-child(#{$i + 1}) {
                background-color: mix($color-black, $color-white, $i*10%);
                color: contrasting-color(mix($color-black, $color-white, $i*10%));
            }
        }
    }
}

// USER TESTING 2017-07-27
.primary-navigation,
.secondary-navigation,
.tertiary-navigation,
.header__search {
    display: none;
}

.nav-mobile {
    @include media("below:medium") {
        width: grid-column-width(6);
    }
    @include media("above:medium") {
        width: grid-column-width(9);
        display: block;
    }
}
