////
/// Stats Block
/// @group components
////


.stats-block {
    color: $color-white;
    text-align: center;

    h2 {
        padding: $measure-medium 0;
        margin-bottom: 0;
        border-bottom: $border-width-hairline $border-style-default $color-flamingo;
        font-size: $font-size-default;
    }
}

.stats-block__grid {
    @extend .grid;
    padding-top: $measure-large;
    padding-bottom: $measure-large;

    & > div {
        align-self: center;
    }
}

.stats-block__grid--thirds {
    & > :nth-child(1) {
        @include grid-column-push(1);
        @include grid-column(2);
    }

    & > :nth-child(2) {
        @include grid-column(4);
        @include grid-column-push(2);
    }

    & > :nth-child(3) {
        @include grid-column-push(3);
        @include grid-column(2);
    }
}

.stats-block__grid--halves {
    @extend .grid--halves;
}

.stats-block__item {
    @include font-family(meta);
    text-align: center;

    &:not(:last-child) {
        padding-bottom: $measure-large;
    }
    &:not(:first-child) {
        padding-top: $measure-large;
    }
}

.stats-block__item__value {
    font-size: 2em;
    font-weight: $font-weight-light;
}

.stats-block__piechart {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: $measure-small;

    .stats-block__piechart__base {
        fill: none;
        stroke: #757373;
    }

    .stats-block__piechart__value {
        @include transition(stroke-dashoffset 1.5s);
        fill: none;
        stroke: $color-quill;
    }

    .stats-block__piechart__text {
        @include font-family(meta);
        align-self: center;
        position: absolute;
        font-size: $font-size-alpha;
    }

    svg {
        width: 100%;
        max-width: 150px;
    }
}

.js .stats-block__piechart__value {
    display: none;

    &.stats-block__piechart__value--jsrun {
        display: block;
    }
}
